.app-container {
  display: grid;
  /* grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr 2fr 3fr 2fr;
	grid-gap: 2px; */
  margin: 10%;
}

.app-title {
  grid-column: 1/-1;
  font-size: 30;
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
}

.item {
  justify-items: center;
  text-align: center;
  background-color: #fdd835;
  border: 1px solid black;
  height: 100%;
}

.counter-label {
  font-size: 10px;
}

.session {
  grid-column: 1/-1;
  grid-row: 3/5;
}
